.divSearch {
  width: 343px;
  height: 50px;
}
.searchInput {
  background-color: #F6F6F6;
  width: 100%;
  height: 100%;
  border-radius: 100px;
  text-indent: 16px;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  border: 1px solid #E8E8E8;
}
.searchInput::placeholder {
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #BDBDBD;
}
