.task-item{
  margin: 15px 0px;
  display: flex;
  justify-content: space-between;
  padding : 8px 40px;
}

.remove-task-button{
  background-color: #ac3b61;
  height: 32px;
  color: white;
  border-radius: 4px;
  width: 70px;
  border:none;
}

.remove-task-button:hover{
  cursor: pointer;
}